<template>
  <div class="member-center">
    <section class="member-center-banner">
      <div class="member-center-user"><img src="/static/image/memberCenter/user.png" @click="toSetting"><span>{{userName}}</span><i class="icon iconfont icon-set setting-icon" @click="toSetting"></i></div>
      <div class="member-center-fufen">
        <div class="fufen-item"><img src="/static/image/memberCenter/mine_medi_heart@3x.png"><span>福心</span><span>{{loveHeart}}</span/></div>
        <div class="fufen-item"><img src="/static/image/memberCenter/mine_medi_coin@3x.png"><span>可用福分</span><span> {{joyScore}}</span></div>
      </div>
    </section>
    <section class="member-center-list">
      <div class="member-center-list-item" v-for="(item,key) in memberList" @click="toPage(item.name)"><img :src="item.icon"><span class="list-title">{{item.label}}</span><span class="icon iconfont icon-more more"></span></div>
    </section>
  </div>
</template>

<script>
  import {getMe} from '../../service/getData'
  export default {
    data () {
      return {
        memberList: [{
          label: '我的收藏',
          icon: '/static/image/memberCenter/mine_list_collect@3x.png',
          name: 'myCollection'
        },
        {
          label: '我的订单',
          icon: '/static/image/memberCenter/mine_list_order@3x.png',
          name: 'myOrder'
        },
        {
          label: '我的推荐',
          icon: '/static/image/memberCenter/mine_list_recommend@3x.png',
          name: 'myRecommend'
        },
        {
          label: '兑换商城',
          icon: '/static/image/memberCenter/mine_list_exchange@3x.png',
          name: 'myCollection'
        },
        {
          label: '我的收益',
          icon: '/static/image/memberCenter/mine_list_profit@3x.png',
          name: 'myEarnings'
        }
        ],
        userName: '',
        loveHeart: '',
        joyScore: '',
      }
    },
    created: function () {
      var _this = this
      /*if (this.$route.params.loveHeart) {
        this.loveHeart = this.$route.params.loveHeart
      }*/
      getMe().then(function (rep) {
        if(rep.data.username){
          if(rep.data.loveHeart){
           _this.loveHeart = rep.data.loveHeart
          }
          if(rep.data.joyScore){
             _this.joyScore = rep.data.joyScore
          }
          if(rep.data.nickName){
             _this.userName = rep.data.nickName
          }
          //    if (_this.$route.params.routerName){//登录后 刷新页面 加载福心等信息
          //      _this.$router.go(0)
          //    }
        }else{
          //session失效，服务器会返回The current Subject is not authenticated.  Access denied.
          _this.$toast('服务器session失效，请重新登录！');
          _this.$router.push({name: 'login'})
        }
        
      }).catch(function () {
        _this.$toast('获取个人信息失败')
      })

    },
    methods: {
      toSetting: function () {
        this.$router.push({
          name: 'userSet',
          params: {
            userName: this.userName
          }
        })
      },
      toPage: function (val) {
        this.$router.push({
          name: val,
          params: {
            name: this.$route.name
          }
        })
      }
    }
  }
</script>

<style>
.member-center {
  height: 100%;
  width: 100%;
  background-color: #F5F5F5;
}

.member-center-banner {
  height: 1.62rem;
  position: relative;
  z-index: 1;
  padding: .2rem .125rem .4rem .125rem;
  background: url(/static/image/memberCenter/mine_top_bg@3x.png);
  -webkit-background-size: 100% 100%;
  background-size: 100% 100%;
}

.member-center-user {
  height: 100%;
  width: 96%;
  margin: 0 auto;
  display: flex;
  color: #FFFFFF;
  position: relative;
  align-items: center;
}

.member-center-user img {
  height: .71rem;
  width: .71rem;
  vertical-align: middle;
  border-radius: 50%;
  margin-right: .2rem;
}

.setting-icon {
  position: absolute;
  right: 0;
  height: .18rem;
  width: .18rem;
  font-size: .18rem;
  line-height: .18rem;
  text-align: center;
  top: 50%;
  transform: translateY(-50%);
}

.member-center-fufen {
  position: absolute;
  bottom: 0;
  height: 1.09rem;
  width: 96%;
  left: 50%;
  z-index: 1;
  padding: .145rem .125rem;
  display: flex;
  line-height: normal;
  align-items: center;
  justify-content: center;
  transform: translate(-50%, 50%);
  background: url(/static/image/memberCenter/mine_medi_bg_shadow@3x.png) no-repeat no-repeat;
  background-size: 100% 100%;
}

.fufen-item {
  flex: 1;
  text-align: center;
}

.fufen-item img {
  height: .21rem;
  width: .21rem;
}

.fufen-item span {
  display: block;
  color: #333;
}

.fufen-item span:last-child {
  color: #FF8D21;
}

.member-center-list {
  position: relative;
  padding: .69rem 0 0 .15rem;
  background-color: #FFFFFF;
}

.member-center-list:before {
  content: '';
  position: absolute;
  width: 100%;
  left: 0;
  height: .01rem;
  background-color: #E5E5E5;
  z-index: 1;
}

.member-center-list-item {
  height: .55rem;
  line-height: .55rem;
  padding-right: .15rem;
  border-top: .01rem solid #E5E5E5;
}

.member-center-list-item:first-child {
  border-top: none;
}

.member-center-list-item img {
  height: .22rem;
  width: .22rem;
  vertical-align: middle;
  margin-right: .1rem;
}

.member-center-list-item .list-title {
  color: #333;
}

.member-center-list-item .more {
  float: right;
}
</style>


